import React, { Component } from 'react';
import { Table } from 'antd';

class TableCreateNewClass extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'M hc phn',
        dataIndex: 'classCode',
        key: 'classCode',
      },
      {
        title: 'Tn hc phn',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
      {
        title: 'S TC',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
      },
      {
        title: 'S tit',
        dataIndex: 'noOfClassCredit',
        key: 'noOfClassCredit',
      },
      {
        title: 'H s',
        dataIndex: 'multiplyBy',
        key: 'multiplyBy',
      },
      {
        title: 'S SV',
        dataIndex: 'soSV',
        key: 'soSV',
      },
      {
        title: 'K hiu tn lp',
        dataIndex: 'classCode',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Input />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'S.tit tun',
        dataIndex: 'periodInWeek',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Input />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'S lp TC',
        dataIndex: 'noOfClassCredit',
        key: 'noOfClassCredit',
      },
      {
        title: 'S SV ti thiu',
        dataIndex: 'minStudent',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Input />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'S SV ti a',
        dataIndex: 'maxStudent',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Input />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'S ca LT/tun',
        dataIndex: 'periodInWeek',
        key: 'periodInWeek',
      },
      {
        title: 'S lp BT, TH, TN',
        dataIndex: 'soLopBTTHTN',
        key: 'soLopBTTHTN',
      },
      {
        title: 'S ca TH, BT, TN/tun',
        dataIndex: 'soCaTHBTTNTuan',
        key: 'soCaTHBTTNTuan',
      },
      {
        title: 'T chn',
        dataIndex: 'isSelfChoose',
        // render: () =>
        //   setTableCreateNewClass.length >= 1 ? (
        //     <Fragment>
        //       <Checkbox />
        //     </Fragment>
        //   ) : null,
      },
      {
        title: 'S.tit LT',
        dataIndex: 'theoryPeriod',
        key: 'theoryPeriod',
      },
      {
        title: 'S.tit TH',
        dataIndex: 'practicePeriod',
        key: 'practicePeriod',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        this.props.handleSelectRow(selectedRowKeys)
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    const { data } = this.props;
    return (
      <div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={data}
          columns={this.columns}
          scroll={{ x: 'true' }}
        />
      </div>
    );
  }
}

export default TableCreateNewClass;



